import React from 'react'
import { I18n } from 'react-i18nify-lite'
import { Link } from 'react-router-dom'
import { Group } from 'prey-stash'
import { Dialog, Loading } from '../assets/Dialog'

 
const initFileContact = () => {
  if(window.Android){
    return window.Android.fileContact();
  }else{
    //alert('No file chosen');
    //return 'No file chosen';
    return 'Screenshot_20200630-102047_Prey.png';
  }
}
const existsFileContact = () => {
  if(window.Android){
    return window.Android.existsFileContact();
  }else{
    return true;
  }
}

export default class Help extends React.Component {

  constructor(props){
    super(props)
    this.state = {
      data: {},
      isLoading: false,
      isFinished: false,
      isAnError: false,
      fileContact: initFileContact(),
      existsFile: existsFileContact(),
    }
  }

  componentDidMount () {
     this.verificateImage();
  }
 
  errorsFor = (name) => {
    const errors = this.state.data[name]
    if(!errors) return
    if(errors.length === 0) return
    return <div ref='error' className="info info-error">{
      errors.map((o,i)=>(
          <p key={i}>{o}</p>
        )
      )}
      </div>
  }

  searchFiles = (e)=>{
     if(window.Android){
      let json=window.Android.searchFiles();
     }else{
      alert("searchFiles");
     }
  }

  handleClick = (e)=>{
    var message=document.getElementById('message').value;
    this.setState({
       data: {} ,
       isLoading: true,
      }, ()=>{setTimeout(this.callHandleClick, 0)
    })
  }

  handleFocus = (e) => {
    this.setState({data: {}})
  }

  callHandleClick = (e)=>{   
    var subject=document.getElementById('subject').value;
    var message=document.getElementById('message').value;
    if(window.Android){
      let json=window.Android.help(''+subject,''+message);
      if(json!==''){
        this.setState({
          data: JSON.parse(json),
          isLoading: false,
        })
      }else{
        this.setState({
          data: {},
          isFinished: true,
          isLoading: false,
        })
        setTimeout(
          () => this.handleClickHelp(),
          5000
        )
      }
    } else {
      //let json='{"attachment":["El archivo seleccionado excede el tamaño máximo"],"subject":["Por favor selecciona un asunto"],"message":["El mensaje debe tener min. 10 caracteres"]}';
      //let json='{"error":["error comunication"]}';
      let json='';
      if(json!==''){
        this.setState({
          data: JSON.parse(json),
          isLoading: false
        })
      }else{
        this.setState({
          data: {},
          isAnError: true,
          isFinished: false,
          isLoading: false,
        })
        setTimeout(
          () => this.handleClickHelp(),
          5000
        )
      }
    }
  }
 
  handleClickHelp = (e)=>{
    if(window.Android){
      window.Android.clickHelp();
    }
    this.props.history.push(`/${I18n._locale}/`);
  }

  handleClickCloseFile = (e)=>{
    if(window.Android){
      window.Android.clickCloseFile();
    }
    this.setState({data: {}});
  }

  verificateImage = () =>{
    this.changeImage();
     
      if(window.Android){
        setTimeout(
          () => this.verificateImage(),
          1000
        );
      }else{
        //alert("verificateImge");
        setTimeout(
          () => this.verificateImage(),
          3000
        )
      }
     
  }
  
  changeImage = () => {
    this.setState({
      fileContact: initFileContact(),
      existsFile: existsFileContact(),
    })
     
  }

  render() {
    return (
      <div className="security fs-onboarding regular inverted help-form">
        <Group>
          <a onClick={this.handleClickHelp}>
          <div className="btn fill help-center help-view">
            <span className="tac">{I18n.t("helpcenter.title")}</span>
            <span className='icon-cancel help-cancel'/>
          </div>
          </a>
        </Group>
        <br/>
        <div className="container">
          {
          this.state.isFinished ? (
            <div className="sign-form content-info">
              <p>
                {I18n.t("helpcenter.description1")} <a href={I18n.t("helpcenter.helpCenterHref")}>{I18n.t("helpcenter.helpCenter")}</a>.<br/>
                {I18n.t("helpcenter.description2")} <a href={I18n.t("helpcenter.helpCenterMailto")}>{I18n.t("helpcenter.helpCenterMail")}</a>.
              </p>
              <div className='text-centered'>
                <h3>{I18n.t(`helpcenter.finished1`)}</h3>
                <p>{I18n.t(`helpcenter.finished2`)}</p>
              </div>
            </div>
          ): this.state.isAnError ? (
            <div className="sign-form content-info">
              <p>
                {I18n.t("helpcenter.description1")} <a href={I18n.t("helpcenter.helpCenterHref")}>{I18n.t("helpcenter.helpCenter")}</a>.<br/>
                {I18n.t("helpcenter.description2")} <a href={I18n.t("helpcenter.helpCenterMailto")}>{I18n.t("helpcenter.helpCenterMail")}</a>.
              </p>
              <div className='text-centered'>
                <h3>{I18n.t(`helpcenter.error1`)}</h3>
                <p>{I18n.t(`helpcenter.error2`)}</p>
              </div>
            </div>
          ) : ( 
            <div className="sign-form">
              <form autoComplete="off" className="form form-session" id="new-session" acceptCharset="UTF-8">
                <p>
                  {I18n.t("helpcenter.description1")} <a href={I18n.t("helpcenter.helpCenterHref")}>{I18n.t("helpcenter.helpCenter")}</a>.<br/>
                  {I18n.t("helpcenter.description2")} <a href={I18n.t("helpcenter.helpCenterMailto")}>{I18n.t("helpcenter.helpCenterMail")}</a>.
                </p>
                <ul className="form vvv">
                  <li>
                    <label htmlFor="subject">{I18n.t("helpcenter.subject")}</label>
                    <select id="subject" name="subject" required tabIndex="1" onFocus={this.handleFocus} >
                      <option value="-1" selected>{I18n.t("helpcenter.subjectHolder")} </option>
                      <option value="issues">{I18n.t("helpcenter.subjectOption1")} </option>
                      <option value="questions">{I18n.t("helpcenter.subjectOption2")} </option>
                      <option value="other">{I18n.t("helpcenter.subjectOption3")} </option>
                    </select>
                    {this.errorsFor("subject")}
                  </li>
                </ul>
                <ul className="form vvv">
                  <li>
                    <label htmlFor="message">{I18n.t("helpcenter.message")}</label>
                    <textarea id="message" name="message" rows="8" cols="50" placeholder={I18n.t("helpcenter.messageHolder")} tabIndex="2" onFocus={this.handleFocus} >
                    </textarea>
                    {I18n.t("helpcenter.messageMin")}
                    {this.errorsFor("message")}
                  </li>  
                </ul>
                <ul className="form vvv">
                  <li className="group-file">
                    <label htmlFor="attachment">{I18n.t("helpcenter.attachment")}</label>
                    <div className="file-int">
                      <input type="button" id="attachment" name="attachment" value="ARCHIVO SELECCIONADO" className="btn input-file l "  tabIndex="3" onFocus={this.handleFocus} onClick={this.searchFiles} /> 
                      <span className='file-name'>{this.state.fileContact}</span>
                      {this.state.existsFile ? (<a onClick={this.handleClickCloseFile} className="close btn transparent default-text icon-cancel" />) : null} 
                    </div>
                    {I18n.t("helpcenter.attachmentHolder")}  
                    {this.errorsFor("attachment")}
                  </li>
                </ul>
                {this.errorsFor("error")}
              </form>
            </div>
        )}
        { 
          this.state.isLoading ? (
            <Loading><code>{I18n.t(`dialog_login.loading`)}</code></Loading>
          ) : null
        }
        
          <Group className="homeOptions">
            <a onClick={this.handleClick} className="btn fill default btn-send">
              <span>{ this.state.isFinished ? I18n.t("helpcenter.btnfinish") : this.state.isAnError ? I18n.t("helpcenter.btnerror") : I18n.t("helpcenter.send")}</span>
            </a>
          </Group>
        </div>
      </div>
  )
 }
}