import React from 'react'
import { I18n } from 'react-i18nify-lite'
import { Loading } from '../assets/Dialog'
import { Group } from '../assets/stash'
import { Link } from 'react-router-dom'

export default class Pin extends React.Component {

  constructor(props){
    super(props)
    this.state = {
      data: {},
      isLoading: false,
      passwordType: 'password',
      capsLockOn: false,
    }
  }

  errorsFor = (name) => {
      const errors = this.state.data[name]
      if(!errors) return
      if(errors.length === 0) return
      return (
        <div className="info info-error">
          <h3><i className="icon-attention" />{I18n.t("pin.error")}</h3>
           
        </div>
      )
  }

  handleChangeInput = () =>{
    this.setState({data: {}})
  }

  handleFocus = (e) => {
    this.setState({data: {}})
  }

  handleShowPassword = (e) => {
    var type=this.state.passwordType
    if (type === 'password') {
      this.setState({
        passwordType: 'text'
      })
    } else {
      this.setState({
        passwordType: 'password'
      })
    }
  }

  verificateTab = (counter) =>{
    counter=counter-1;
    this.changeTab()
    if(counter>0){
      if(window.Android){
        setTimeout(
          () => this.verificateTab(counter),
          1000
        );
      }else{
        alert("verificateTab:"+counter);
        setTimeout(
          () => this.verificateTab(counter),
          1000
        )
      }
    }
  }

  touch = () => {
    if(window.Android){
      window.Android.touch();
    }
    this.setState({
        capsLockOn: false
    });
    if(window.Android){
      this.verificateTab(20)
    }else{
      this.verificateTab(1)
    }
  }

  changeTab = () => {
    var capsLock=this.state.capsLockOn
    if(window.Android){
      capsLock=window.Android.capsLockOn();
    }else{
      capsLock=!capsLock;
    }
    this.setState({
      capsLockOn: capsLock
    })
  }

  handleClickLogin = (e)=>{
    var passlogin=document.getElementById('passlogin').value;
    let json='';
    this.setState({
      data: {}
    })
    // let json='';
    if(window.Android){
      json=window.Android.unpin(''+passlogin);
    }else{
      json='{"error":["Contraseña incorrecta. Inténtalo nuevamente."]}';
    }
    if(json!==''){
      this.setState({
        data: JSON.parse(json),
        isLoading: false
      })
    }

  }

  render() {
    return  <div className="login fs-onboarding regular inverted">
    <div className="column">
     <div className="sign-form">
      <h1 className="lock info text-warning">
        <small>{I18n.t('lock.access')}</small>
        <span>{I18n.t('lock.denied')}</span>
      </h1>
      <p>{I18n.t('pin.text1')}</p>
      <div className="sign-form">
        <form autoComplete="off" className="form form-lock" id="lock" acceptCharset="UTF-8">
                <ul className="form hvv">
                  <li className="superpassword">
                    <label className="tag-label" htmlFor="passlogin">{I18n.t("pin.pin")}
                    </label>
                    <div className="flex-container"> 
                      <div className="flex-item" >
                        <input type={this.state.passwordType} size="5" maxlength="1" name="password1" id="passlogin" tabIndex="1" />
                      </div>
                      <div className="flex-item" >
                        <input type={this.state.passwordType} size="5" maxlength="1" name="password2" id="passlogin2" tabIndex="2" />
                      </div>
                      <div className="flex-item" >
                        <input type={this.state.passwordType} size="5" maxlength="1" name="password3" id="passlogin3" tabIndex="3" />
                      </div>
                      <div className="flex-item" >
                        <input type={this.state.passwordType} size="5" maxlength="1" name="password4" id="passlogin4" tabIndex="4"  />
                      </div>
                      <div className="flex-item" >
                        <a  onClick={this.handleShowPassword}><p className={`btn  ${this.state.passwordType === 'text' ? 'success':''}`}><i className="icon-eye" /></p></a>
                      </div>    
                    </div> 
                    <a className="btn flex icon-lock-open full" onClick={this.handleClickLogin}>
                      <span>{I18n.t(`pin.unlock`)}</span>
                    </a>
                    {
                      this.state.capsLockOn ? (
                        <div className="info info-warning"><p>{I18n.t("signup.capsLockIsOn")}</p></div>
                      ) : null
                    }
                    {this.errorsFor("error")}
                  </li>
                </ul>
        </form>
          {
            this.state.isLoading ? (
              <Loading><code>{I18n.t(`dialog_login.loading`)}</code></Loading>
            ) : null
          }
      </div>  
     </div>
    </div>
    <Group className="homeOptions">
      <Link to={`/${I18n._locale}/`} className="btn fill default">
        <i className="icon-cancel" />
        <span>{I18n.t("close")}</span>
      </Link>
    </Group>
  </div>
 }

}
