import React from 'react'
import { Link } from 'react-router-dom'
import { I18n } from 'react-i18nify-lite'
import { Fold, Group } from '../assets/stash'

const initAndroid10OrAbove = () => {
  if(window.Android){
    return window.Android.initAndroid10OrAbove()
  } else {
    return true
  }
}

const initXiaomi = () => {
  if(window.Android){
    return window.Android.initXiaomi()
  } else {
    return false
  }
}

const initHuawei = () => {
  if(window.Android){
    return window.Android.initHuawei()
  } else {
    return true
  }
}

class Privacy extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      showPrivacy: true,
      isAndroid10OrAbove: initAndroid10OrAbove(),
      xiaomi_alert: initXiaomi(),
      huawei_alert: initHuawei(),
    }
  }

  render() {
    return (
      <div className="security fs-onboarding regular inverted">
        <div className="column">
          <h4 className="heading">{I18n.t("permissions.title")}</h4>

          <p>{I18n.t("permissions.privacy_info1")}</p> 
            <p>{I18n.t("permissions.privacy_info2")}</p> 
           

          {(this.state.isAndroid10OrAbove) ? (
          <div>
          <Fold
            initial="open"
            className="more-info"
            label={`${I18n.t("permissions.category_location")}`}
            button="fill transparent l default-text fold-trigger"
            >
            <Group>
                    <div className="item">   
                      <p>{I18n.t("permissions.location.background1")}</p>
                      <p>{I18n.t("permissions.location.background2")}</p>
                    </div>              
            </Group>
          </Fold><div className="line-through" /></div>
          ):null}
          {(this.state.xiaomi_alert) ? (
          <div>
          <Fold
            initial="open"
            className="more-info"
            label={I18n.t("permissions.xiaomi.head")}
            button="fill transparent l default-text fold-trigger"
          >
            <Group>
                    <div className="item">
                      <p dangerouslySetInnerHTML={{__html: I18n.t('permissions.xiaomi.body')}}/>
                    </div>                
            </Group>
          </Fold>
          <div className="line-through" />
          </div>
          ):null}
          {(this.state.huawei_alert) ? (
          <div>
          <Fold
            initial="open"
            className="more-info"
            label={I18n.t("permissions.huawei.head")}
            button="fill transparent l default-text fold-trigger"
          >
            <Group>
                    <div className="item">
                      <p dangerouslySetInnerHTML={{__html: I18n.t('permissions.huawei.body')}}/>
                    </div>              
            </Group>
          </Fold>
          <div className="line-through" />
          </div>
          ):null}
          <Fold
            initial="open"
            className="more-info"
            label={`
              ${I18n.t("permissions.category_permission")}
            `}
            button="fill transparent l default-text fold-trigger"
            >
            <Group>
              
                    <div className="item">
                      <h5>{I18n.t("permissions.location.head")}</h5>
                      <p>{I18n.t("permissions.location.body")}</p>
                    </div>
                    <div className="item">
                      <h5>{I18n.t("permissions.camera.head")}</h5>
                      <p>{I18n.t("permissions.camera.body")}</p>
                    </div>
                
                    <div className="item">
                      <h5>{I18n.t("permissions.storage.head")}</h5>
                      <p>{I18n.t("permissions.storage.body")}</p>
                    </div>
                
            </Group>
          </Fold>
          <div className="line-through" />
          <Fold
            initial="open"
            className="more-info"
            label={I18n.t("permissions.administrator_permission.head")}
            button="fill transparent l default-text fold-trigger"
          >
            <Group>
              
                    <div className="item">
                      <p>{I18n.t("permissions.administrator_permission.body")}</p>
                    </div>
                
            </Group>
          </Fold>
          <div className="line-through" />
          <Fold
            initial="open"
            className="more-info"
            label={`
              ${I18n.t("permissions.over.head")} ${I18n.t("permissions.over.and")} ${I18n.t("permissions.accesibility.head")}
            `}
            button="fill transparent l default-text fold-trigger"
          >
            <Group>
               
                    <div className="item">
                      <h5>{I18n.t("permissions.over.head")}</h5>
                      <p>{I18n.t("permissions.over.body")}</p>
                    </div>

                    <div className="item">
                      <h5>{I18n.t("permissions.accesibility.head")}</h5>
                      <p>{I18n.t("permissions.accesibility.body")}</p>
                    </div>

                 
            </Group>
             
          </Fold>
         

        </div>
        <Group className="homeOptions">
          <Link to={`/${I18n._locale}/`} className="btn fill default">
            <i className="icon-cancel" />
            <span>{I18n.t("close")}</span>
          </Link>
        </Group>
      </div>
    )
  }
}

export default Privacy
