import React from 'react'
import { I18n } from 'react-i18nify-lite'
import { Link } from 'react-router-dom'
import { Group } from 'prey-stash'
import { Dialog, Loading } from '../assets/Dialog'


const initDownloadedServices = () => {
  if(window.Android){
    return window.Android.isAccessibilityInformationType("downloaded_services")
  } else {
    return true
  }
}

const initDownloadedApps = () => {
  if(window.Android){
    return window.Android.isAccessibilityInformationType("downloaded_apps")
  } else {
    return false
  }
}

const initInstalledApps = () => {
  if(window.Android){
    return window.Android.isAccessibilityInformationType("installed_apps")
  } else {
    return false
  }
}

const initMore = () => {
  if(window.Android){
    return window.Android.isAccessibilityInformationType("more")
  } else {
    return false
  }
}

export default class Accessibility extends React.Component {

  constructor(props){
    super(props)
    this.state = {
      data: {},
      initDownloadedServices: initDownloadedServices(),
      initDownloadedApps: initDownloadedApps(),
      initInstalledApps: initInstalledApps(),
      initMore: initMore(),
    }
  }

  handleClickAccessibilitySkip = (e)=>{
  	if (window.Android) {
      window.Android.accessibilitySkip();
    } else {
      alert("ola oso next");
    }
  }

  handleClickAccessibilityDeny = (e)=>{
  	if (window.Android) {
      window.Android.accessibilityDeny();
    } else {
      alert("ola oso deny");
    }
  }

  handleClickAccessibilityAgree = (e)=>{
  	if (window.Android) {
      window.Android.accessibilityAgree();
    } else {
      alert("ola oso agree");
    }
  }

  render() {
    return (
		<div className="security fs-onboarding regular inverted help-form">
		    <Group>
		        <a onClick={this.handleClickAccessibilitySkip}>
		          <div className="btn fill help-center accessibility-view">
		            <span className="tac2">{I18n.t("accesibility.skip")}</span>
		            <span className='icon-right-open-big'/>
		          </div>
		        </a>
		    </Group>
		    <br/>
	        <div className="container">
	        	<h4 className="heading">
            		{I18n.t("accesibility.title_1")}
          		</h4>
          		<br/>
          		<p>
            		{I18n.t("accesibility.text1_1")}
          		</p>
          		<br/>
          		

            
            		<p>
                <span dangerouslySetInnerHTML={{__html: I18n.t('accesibility.text2_1')}}/><br/>
                <span dangerouslySetInnerHTML={{__html: I18n.t('accesibility.text2_2')}}/><br/>
                { this.state.initDownloadedServices ? (
                  <span dangerouslySetInnerHTML={{__html: I18n.t('accesibility.text2_3_1')}}/>
                ) : null}
                { this.state.initDownloadedApps ? (
                  <span dangerouslySetInnerHTML={{__html: I18n.t('accesibility.text2_3_2')}}/>
                ) : null}
                { this.state.initMore ? (
                  <span dangerouslySetInnerHTML={{__html: I18n.t('accesibility.text2_3_3')}}/>
                ) : null}
                { this.state.initInstalledApps ? (
                  <span dangerouslySetInnerHTML={{__html: I18n.t('accesibility.text2_3_4')}}/>
                ) : null}

                <br/>
                <span dangerouslySetInnerHTML={{__html: I18n.t('accesibility.text2_4')}}/><br/>
                <span dangerouslySetInnerHTML={{__html: I18n.t('accesibility.text2_5')}}/> 
                </p>
            

 

           
          		<br/>
          		<h5>
            		{I18n.t("accesibility.text3_1")}
          		</h5>
			</div>
			<Group className="homeOptions">
          		<Group className="ctas">
            		<Link to="#" onClick={this.handleClickAccessibilityDeny} className="btn alt flex">
              			<span>{I18n.t("accesibility.deny")}</span>
            		</Link>
            		<Link to="#" onClick={this.handleClickAccessibilityAgree} className="btn flex">
              			<span>{I18n.t("accesibility.agree")}</span>
            		</Link>
          		</Group>
        	</Group>
        </div>
		)
  }
}