  import React from 'react'
// import { Link } from 'react-router-dom'
// import logomono from '../images/branding/prey-logo-monotono-alt.svg'
import { I18n } from 'react-i18nify-lite'
import { Loading } from '../assets/Dialog'


const initVerifyLock = () => {
  if(window.Android){
    window.Android.verifyLock();
  }else{
    //alert('verifyLock');

  }
}

export default class Lock extends React.Component {

  constructor(props){
    super(props)
    this.state = {
      data: {},
      isLoading: false,
      passwordType: 'password',
      capsLockOn: false,
      verifyLock: initVerifyLock(),
      isAlert:false,
      alertText:'',
    }

  }

  componentDidMount () {
    this.verificateAlert(0)
  }

  errorsFor = (name) => {
      const errors = this.state.data[name]
      if(!errors) return
      if(errors.length === 0) return
      return (
        <div className="info info-error">
          <h3><i className="icon-attention" />{I18n.t("password_wrong_title")}</h3>
          <p>{I18n.t("password_wrong_body")}</p>
        </div>
      )
  }

  handleChangeInput = () =>{
    this.setState({data: {}})
    var passlogin=document.getElementById('passlogin').value;
    if(window.Android){
      window.Android.inputwebview(passlogin,'lock');
    }else{
      alert('passlogin:'+passlogin);
    }
  }

  handleFocus = (e) => {
    this.setState({data: {}})
  }

  handleShowPassword = (e) => {
    var type=this.state.passwordType
    if (type === 'password') {
      this.setState({
        passwordType: 'text'
      })
    } else {
      this.setState({
        passwordType: 'password'
      })
    }
  }

  verificateTab = (counter) =>{
    counter=counter-1;
    this.changeTab()
    if(counter>0){
      if(window.Android){
        setTimeout(
          () => this.verificateTab(counter),
          1000
        );
      }else{
        alert("verificateTab:"+counter);
        setTimeout(
          () => this.verificateTab(counter),
          1000
        )
      }
    }
  }

  verificateAlert = (counter) =>{
    var verificateAlert_='';
    if(window.Android){
      verificateAlert_=window.Android.verificateAlert();
    }else{
      verificateAlert_='';
    }
    if(verificateAlert_==''){
      this.setState({
        alertText: '',
        isAlert:false
      })
    }else{
      this.setState({
        alertText: verificateAlert_,
        isAlert:true
      })
    }
    setTimeout(
          () => this.verificateAlert(counter),
          5000
        )
  }

  touch = () => {
    if(window.Android){
      window.Android.touch();
    }
    this.setState({
        capsLockOn: false
    });
    if(window.Android){
      this.verificateTab(20)
    }else{
      this.verificateTab(1)
    }
  }

  changeTab = () => {
    var capsLock=this.state.capsLockOn
    if(window.Android){
      capsLock=window.Android.capsLockOn();
    }else{
      capsLock=!capsLock;
    }
    this.setState({
      capsLockOn: capsLock
    })
  }

  handleClickLogin = (e)=>{
    var passlogin=document.getElementById('passlogin').value;
    let json='';
    this.setState({
      data: {}
    })
    // let json='';
    if(window.Android){
      json=window.Android.lock(''+passlogin);
    }else{
      json='{"error":["Contraseña incorrecta. Inténtalo nuevamente."]}';
      //json='{"ok":"ok"}';
    }
    if(json==='{"ok":"ok"}'){
      this.setState({
        isLoading: false
      });
      this.props.history.push(`/${I18n._locale}/index`);        
    }else{
      if(json!==''){
        this.setState({
          data: JSON.parse(json),
          isLoading: false
        })
      }
    }


  }
 
  render() {
    return  <div className="login fs-onboarding regular inverted">
    <div className="column">
     <div className="sign-form">
      <h1 className="lock info text-warning">
        <small>{I18n.t('lock.access')}</small>
        <span>{I18n.t('lock.denied')}</span>
      </h1>
      <div className="sign-form">
        <form autoComplete="off" className="form form-lock" id="lock" acceptCharset="UTF-8">
          <ul className="form hvv">
            <li className="superpassword">
              <p>{I18n.t('lock.text1')}</p>
              <label className="tag-label" htmlFor="passlogin">
                <input type={this.state.passwordType} placeholder={I18n.t("lock.password")} name="password1" id="passlogin" tabIndex="1" onFocus={this.handleFocus}  onClick={this.touch} onChange={this.handleChangeInput}/>
                <a  onClick={this.handleShowPassword}><p className={`btn s square ${this.state.passwordType === 'text' ? 'success':''}`}><i className="icon-eye" /></p></a>
              </label>

                <a className="btn flex icon-lock-open full" onClick={this.handleClickLogin}>
                <span>{I18n.t(`lock.unlock`)}</span>
              </a>
              {
                this.state.capsLockOn ? (
                  <div className="info info-warning"><p>{I18n.t("signup.capsLockIsOn")}</p></div>
                ) : null
              }
              {this.errorsFor("error")}
            </li>
          </ul>
        </form>

          {
            this.state.isLoading ? (
              <Loading><code>{I18n.t(`dialog_login.loading`)}</code></Loading>
            ) : null
          }

      </div>
      
      {
          this.state.isAlert ? (
          <div className="info notice-custom"><p>{this.state.alertText}</p></div>
          ) : null
        }
      </div>
    </div>
    
  </div>

 }

}
